@import url('https://fonts.googleapis.com/css2?family=Bungee+Spice&display=swap');
body{
  background-color: #000000!important;
  font-family: 'Bungee Spice', cursive!important;
}
.header{
height: 10vh!important;
background-color: aqua!important;
display: flex!important;
justify-content: center!important;
align-items: center!important;
}
.btnoff{
  display: none;
}
.main{
  height: 80vh!important;
  display: flex!important;
  flex-direction: column!important;
  align-items: center!important;
  justify-content: space-evenly!important;
}

.bulbimg{
  height: 20vh!important;
  
}
.myBorder{
  border-radius: 50%!important;
  border-color: rgb(233,197,37)!important;
}
.boxShadow1{
  box-shadow: rgba(233, 197, 37, 0.56) 0px 0px 105px 81px;
  
}
.boxShadow2{
  box-shadow: rgba(233, 197, 37, 0.25) 0px 54px 55px, rgba(233, 197, 37, 0.12) 0px -12px 30px, rgba(233, 197, 37, 0.12) 0px 4px 6px, rgba(233, 197, 37, 0.17) 0px 12px 13px, rgba(233, 197, 37, 0.09) 0px -3px 5px;

  
}
.footer{
  height: 10vh!important;
  background-color: aqua!important;
  display: flex!important;
  justify-content: center!important;
  align-items: center!important;
  font-family: 'Bungee Spice', cursive!important;
  font-size: 2.1rem!important;
  }

  @media (max-width: 768px) { 
    .footer{
     
      font-size: 0.9rem!important;
      }
  }